.popular{
     display: flex;
     align-items: center;
     flex-direction: column;
     justify-content: center;
     gap: 15px;
}
.popular h1{
     color: #171717;
     font-size: 50px;
     font-weight: 100;
}
.popular hr{
     width: 200px;
     height: 6px;
     border-radius: 20px;
     background: #252525;
     
}
.popular-item{
     margin-top: 30px;
     margin-left: 50px;
     display: flex;
     align-items: center;
     gap: 20px;
}
@media(max-width:1280px){
         .popular{
           height:70vh
         }
         .popular h1 {
           font-size: 35px;
         }
         .popular hr{
           width:160px;
            height: 4px;
         }
         .popular-item{
           gap:20px;
           margin-top: 30px;
         }
}
@media(max-width:1024px){
     .popular{
       height:60vh
     }
     .popular h1 {
       font-size: 30px;
     }
     .popular hr{
       width:150px;
        height: 2px;
     }
     .popular-item{
       gap:15px;
       margin-top: 20px;
     }
}
@media(max-width:800px){
     .popular{
       height:50vh;
       gap:6px;
     }
     .popular h1 {
       font-size: 20px;
     }
     .popular hr{
       width:100px;
      
     }
     .popular-item{
       gap:5px;
       margin-top: 8px;
     }
}
@media(max-width:500px){
      .popular{
          height:90vh ;
      }
      .popular-item{
           display: grid;
           grid-template-columns: 1fr 1fr;
           gap:20px
      }
}
